@import './sass/styles.scss';

body {
  margin: 0;
  padding: 0;

  font-family: $font-display , -apple-system, BlinkMacSystemFont, "Segoe UI",  "Oxygen","Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
