.FormBoxShadow {
  -moz-box-shadow: 0px 0px 2px #ccc;
  box-shadow: 0px 0px 2px #ccc;
  border-radius: '5px';
 
}

.Root {
  width: 100%;
  display: grid;
  grid-template-columns: 22.5rem 1fr;
  grid-template-rows: min-content min-content;
  grid-column-gap: 2rem;
  grid-row-gap: 4rem;
  
}